  created() {
    axios.get('http://localhost:8070/api/statistics/vrp-ekz-prog/vrpoblast', {
        params: {
            scenario: ''
        }
    })
    .then((arrRegions) => {
        arrRegions.data.map(d => {
            if (d['indicator'] == this.myIndicator && d['industry'] == ' ' && d['investments'] == '') {
                this.years.push(d['year'])
                this.goods.push(d['value'])
            }
            else if (d['indicator'] == this.myIndicator && d['industry'] == '' && d['investments'] == '') {
                this.industry.push(d['value'])
