            else if (d['indicator'] == this.myIndicator && d['industry'] == '' && d['investments'] == '') {
                this.building.push(d['value'])
            }
            else if (d['indicator'] == this.myIndicator && d['industry'] == ' ' && d['investments'] == '') {
                this.service.push(d['value'])
            }
            else if (d['indicator'] == this.myIndicator && d['industry'] == '  ' && d['investments'] == '') {
                this.communication.push(d['value'])
            }
            else if (d['indicator'] == this.myIndicator && d['industry'] == '   ' && d['investments'] == '') {
                this.estates.push(d['value'])
            }
            else if (d['indicator'] == this.myIndicator && d['industry'] == ' ' && d['investments'] == '') {
                this.other.push(d['value'])
            }
            else if (d['indicator'] == this.myIndicator && d['industry'] == ' ' && d['investments'] == '') {
                this.wholesale.push(d['value'])
            }
            else if (d['indicator'] == this.myIndicator && d['industry'] == ' ' && d['investments'] == '') {
                this.retail.push(d['value'])
            }
            else if (d['indicator'] == this.myIndicator && d['industry'] == '  ' && d['investments'] == '') {
                this.transport.push(d['value'])
            }
            else if (d['indicator'] == this.myIndicator && d['industry'] == ' ' && d['investments'] == '') {
                this.taxes.push(d['value'])
            }
        })
        // console.log(this.estates)
    })
    .catch(error => console.log(error))
  }
};
