), ':'totalNumberOfDayBedsInHospitalOrganizations', '    , ':'lifeExpectancyAtBirth', '  ,  100 . ':'morbidityFromAcuteMyocardialInfarction', ', ':'pediatric', '    ,  100 . ':'circulatoryMortality', '  ,     ,  100 . ':'injuriesMortality', '    ,  100 . ':'cancerMortality', '   ,  100 . ':'tuberculosisMortalityRate', ' ,  1 . ':'mortalityRate', ', ':'paramedics', '   , ':'numberOfSelfSupportingSystemMZ', ' , ':'numberOfPrivateOrganizations', '  ( )( )':'numberOfDoctorsFromSystemMZ  ', '  ( ), ':'numberOfDoctorsFromAllDepartments', '  ( )':'numberOfNursesFromSystemMZ', '   ( )':'numberOfNursesFromAllDepartments'},

      indicatorsByYears: {' ,  1 . ':'mortalityRate', ' ,   100 . ':'maternalMortality', ' ,   1 . ':'infantMortality', ' ,   100 . ':'incidenceTuberculosis', '    , ':'lifeExpectancyAtBirth', ' ,   1 . ':'childMortality', '   ,   100 . ':'tuberculosisMortalityRate', '    ,   100 . ':'circulatoryMortality', '    ,   100 . ':'cancerMortality', '  ,   1 . ':'naturalPopulationGrowth', '  ,   100 . ':'heartDiseaseMortality', '  ,     ,   100 . ':'injuriesMortality', '  ( ), ':'numberOfDoctorsFromAllDepartments', '  ( ):  , ':'numberOfDoctorsFromSystemMZ', '    ,   100 . ':'provisionOfDoctorsFromAllDepartments', '    :  ,   100 . ':'provisionOfDoctorsFromSystemMZUnit', '   ( ), ':'numberOfNursesFromAllDepartments', '  :  , ':'numberOfNursesFromSystemMZ', '    ( ),   10 . ':'provisionOfNursesFromAllDepartments', '   :  ,   10 . ':'provisionOfNursesFromSystemMZ', '    , ':'numberOfProvidingInpatientCare', '    :  , ':'numberOfProvidingInpatientCareFromSystemMZ', '   , ':'totalNumberOfHospitalBeds', '    ( ), ':'numberOfHospitalBedsFromSystemMZ', '     :  , ':'numberOfSelfSupportingSystemMZ', ' , ':'numberOfPrivateOrganizations', '   ,   10 . ':'provisionOfHospitalBeds', '    ( ),   10 . ':'provisionOfHospitalBedsSystemMZ', '      ( ), . ':'numberOfResidentsSystemMZ', '      ( ), . ':'numberOfResidentsState', '      ( ), . ':'numberOfResidentsPrivate', ' ,    , ':'numberOfOrganizationsWithDayHospitalBeds', '        ( ), ':'totalNumberOfDayBedsInHospitalOrganizations', ' ,  -  ( ), ':'numberOfOrganizationsWithPatientPolyclinicHelp', ' ,  -  ( ), ':'numberOfOrganizationsWithPatientPolyclinicHelpSystemMZ', ' ,  -  ( ), ':'numberOfOrganizationsWithPatientPolyclinicHelpOthers', ' ,  -  ( ), ':'numberOfOrganizationsWithPatientPolyclinicHelpPrivate', '      ( ),  1000 ,  1000 ':'numberOfVisitDoctorsFor1000', '      ( ),  1 ,  1 ':'numberOfVisitDoctorsFor1', ' - ,    , ':'numberOfPolyclinicsWithDayHospitalBeds', '     , ':'totalNumberDayPoliclinicBeds', '  ,   100 . ':'totalMorbidityOfPopulation', '   ,   100 . ':'incidenceOfCancer', '      ,   100 . ':'morbidityFromMentalDisorders', '          ,   100 . ':'morbidityFromMentalDisordersWithSubstanceUse', '  ,     ,   100 . ':'morbidityFromInjuries', '    ,   100 . ':'morbidityFromBloodCirculation', ' ,   100 . ':'morbidityFromArterialHypertension', '     (  ),   100 . ':'morbidityFromCoronaryHeartDisease', '  ,   100 . ':'morbidityFromAcuteMyocardialInfarction', '  , ':'numberOfIndependentStations', '  , ':'numberOfEmergencyDepartments', '  , ':'numberOfEmergencyBrigades', ', ':'medical', ', ':'pediatric', ', ':'paramedics', '    , ':'numberOfcompletedEmergencyVisits', '  ,    ,   1 .':'numberOfServedPersonsByEmergencyDepartments', ',  1 . ':'fertility', ' -, %':'prevalenceOfInfection', '  (  ), ':'numberOfDoctorsInPrivateOrganizations', '  , %':'medicalStaffing', '   , ':'numberOfMedicalStaffPositions', '   , ':'numberMedicalPositionsHeld', '   (  ), ':'numberOfNursesInPrivateOrganizations', ' ,    ( ), ':'numberOfProvidingInpatientCareOthers', ' ,   ""    ,   100 . ':'numberOfPatientsInTheD', '  0-14 ':'numberOfPatientsFrom0TO14', '  15-17 ,   100 . ':'numberOfPatientsFrom15To17', '   18 ,   100 . ':'numberOfPatientsAfter18', '  , ':'totalNumberOfPharmacies', ' , ':'numberOfStatePharmacies', ' , ':'numberOfPrivatePharmacies'},
